body,html {
    line-height: 1;
    font-weight: 200;
    font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', Arial, Helvetica, sans-serif;
}

.clearfix {
    display: inline-block;
    &:after {
        display: block;
        content: ".";
        height: 0;
        line-height: 0;
        clear: both;
        visibility: hidden;
    }
}

@media (-webkit-min-device-pixel-radio: 1.5),(min-device-pixel-radio: 1.5) {
    .border-1px {
        &::after {
            -webkit-transform: scaleY(0.7);
            transform: (0.7);
        }
    }
}

@media (-webkit-min-device-pixel-radio: 2),(min-device-pixel-radio: 2) {
    .border-1px {
        &::after {
            -webkit-transform: scaleY(0.5);
            transform: (0.5);
        }
    }
}


